@import "grid-settings";
@import "neat";
@import "variables";
header {
	.header-wrapper {
		padding: 50px 20px;
		@include media($mobile) {
			padding: 20px 20px;
		}
		@include outer-container();
		nav {
			@include span-columns(12);
			position: relative;
			#logo {
				width: 160px;
				height: auto;
				float: left;
			}
			#menu-icon {
				float: right;
				display: none;
				@include media($md) {
					display: block;
				}
				@include media($mobile) {
					padding: 10px;
				}
			}
			ul {
				width: calc(100% - 160px);
				list-style: none;
				float: right;
				margin: 0;
				padding: 0;
				text-align: right;
				@include media($md) {
					width: calc(100% + 40px);
					text-align: center;
					position:absolute;
					top: 50px;
					left: -20px;
					background-color: white;
					padding: 50px 0;
    				z-index: 10;
				}
			}
			li {
				font-family: $PosteramaBlack;
				color: $blue;
				font-size: $Baseline;
				padding: 10px 25px;
				display: inline-block;
				@include media($lg) {
					padding: 10px 3px;
				}
				@include media($md) {
					padding: 5px;
					display: block;
				}
			}
		}
		.button-blue {
			background-color: #003DA6;
			color: white;
			border: none;
			border-radius: 3px;
			box-shadow: 0 1px 3px 0 rgba(107, 122, 148, 0.3), 0 2px 5px 0 rgba(35, 73, 139, 0.08);
			height: 50px;
			font-family: "PosteramaTextBlack", sans-serif;
			font-size: 16px;
			font-weight: 900px;
			line-height: 22px;
			cursor: pointer;
			margin: -12px;
			display: inline-block;
			padding: 0 30px;
			@include media($md) {
				margin: 0;
				padding: 0 10px;
				height: 40px;
			}
		}
	}
}